<template>
  <div id="imageContent">
    <a-row style="margin-bottom: 16px">
      <a-col :flex="4">
        <left-content />
      </a-col>
      <a-col :flex="10">
        <div v-if="showElement" id="middleContent">
          <div id="contentItems">
            <div id="middleText">
              <div id="text-1">
                <div id="text-1-1">2023.8.22</div>
                <div id="text-1-2">我们的相遇</div>
              </div>
              <div id="text-1">
                <div id="text-1-2">我们的</div>
                <div id="text-2-1">100</div>
                <div id="text-2-2">天</div>
                <div id="text-1-1">2023.11.30</div>
              </div>
              <div id="text-1">
                <div id="text-1-2">到今天已经</div>
                <div id="text-3-1">{{ daysDifference }}</div>
                <div id="text-1-2">天了呢</div>
              </div>
            </div>
            <a-space direction="vertical" size="large">
              <a-input
                :style="{
                  width: '400px',
                  height: '40px',
                  marginTop: '40px',
                }"
                :placeholder="defaultText"
                allow-clear
                @press-enter="imgSearch"
                @focus="clearPlaceHolder"
                v-model="inputNumber"
              />
            </a-space>
          </div>
        </div>
        <div v-else id="imageDisplay">
          <!-- 图片展示区，仅当 inputNumber 有值时显示 -->
          <img :src="imageUrl" alt="Displayed Image" v-if="inputNumber" />
        </div>
      </a-col>
      <a-col :flex="6">
        <div v-if="fileContent" id="rightSearchArea">
          <a-button type="primary" shape="round" @click="init()"
            >🐤 返回首页</a-button
          >
        </div>
        <div id="rightText" v-if="fileContent">
          <div id="textTitle">那天想说的话</div>
          <div>
            <icon-quote
              :style="{ fontSize: '32px' }"
              :stroke-width="2"
              stroke-linecap="square"
            />
          </div>
          <div id="textContent">
            <div v-for="(paragraph, index) in paragraphs" :key="index">
              {{ paragraph }}
            </div>
          </div>
          <div id="textBottom">Love U ❤️</div>
        </div>
        <div v-else id="video">
          <video controls>
            <source :src="videoUrl" type="video/mp4" />
          </video>
