<script setup lang="ts">
import { ref, onMounted, computed } from "vue";
import LeftContent from "@/components/LeftContent.vue";
import axios from "axios";

const defaultText = ref(" ❤️ 写一个你想到的数字 ^-^");
const showElement = ref(true);
const fileContent = ref("");
const images = ref([]);
const imagesList = ref([]);
const textList = ref([]);
const daysDifference = ref(0);
const inputNumberRight = ref("");
// 输入的数字
const inputNumber = ref("");
const videoUrl = ref(`${process.env.BASE_URL}assets/files/123.mp4`);
// 图片匹配数字
const imageUrl = computed(() => {
  if (inputNumber.value < 100) {
    const defaultImage = imagesList.value.find((img) => img.name === "99.png");
    return defaultImage ? defaultImage.url : "";
  }
  const image = imagesList.value.find(
    (img) => img.name === `${inputNumber.value}.png`
  );
  if (image) {
    return image.url;
  } else {
    const fallbackImage = imagesList.value.find((img) => img.name === "88.png");
    return fallbackImage ? fallbackImage.url : "";
  }
});
// 搜索图片
const imgSearch = (key: string) => {
  imageUrl.value = new URL(`../assets/images/118.png`, import.meta.url).href;
  showElement.value = false; // 隐藏输入框和其他div元素
  defaultText.value = "❤️ 写一个你想到的数字 ^-^";
  fetchAndShowFileContent();
};

const fetchAndShowFileContent = async () => {
  let fileUrl;
  console.log("get");

  // 如果 inputNumber.value 小于 100，尝试获取 '11.txt' 的 URL
  if (inputNumber.value < 100) {
    const file11 = textList.value.find((file) => file.name === "99.txt");
    fileUrl = file11 ? file11.url : null;
  } else {
    // 尝试根据 inputNumber.value 获取匹配的文件 URL
    const matchedFile = textList.value.find(
      (file) => file.name === `${inputNumber.value}.txt`
    );
    fileUrl = matchedFile ? matchedFile.url : null;
  }

  // 如果没有找到匹配的文件，尝试获取 '22.txt' 的 URL
  if (!fileUrl) {
    const file22 = textList.value.find((file) => file.name === "88.txt");
    fileUrl = file22 ? file22.url : null;
  }

  // 如果找到了文件的 URL，则尝试获取其内容
  if (fileUrl) {
    try {
      const response = await fetch(fileUrl);
      fileContent.value = await response.text();
    } catch (error) {
      console.error("Error fetching file:", error);
      fileContent.value = "文件内容加载失败";
    }
  } else {
    fileContent.value = "未找到匹配的文件";
  }
};

// 清空默认值
const clearPlaceHolder = (key: string) => {
  defaultText.value = "";
};

// 计算属性，用于将文本内容分割成段落数组
const paragraphs = computed(() => {
  console.log("feile", fileContent);
  // 匹配以~结尾的文本段落，包括~在内
  const pattern = /[^~]+~/g;
  const matches = fileContent.value.match(pattern);
  console.log("math", matches);
  return matches || [];
});

const init = () => {
  fileContent.value = "";
  imageUrl.value = "";
  showElement.value = true;
  inputNumber.value = "";
};
//换算时间和文本读取
onMounted(async () => {
  //换算时间
  const startDate = new Date("2023-08-22");
  const currentDate = new Date();
  const timeDifference = currentDate.getTime() - startDate.getTime();
  daysDifference.value = Math.floor(timeDifference / (1000 * 60 * 60 * 24));

  // 读取图片文件
  const repoPath = "julia8621xu/honey-images"; // 你的GitHub用户名和仓库名
  const folderPath = "images"; // 仓库中存放图片的文件夹路径
  const branchName = "files"; // 分支名
  const githubApiUrl = `https://api.github.com/repos/${repoPath}/contents/${folderPath}?ref=${branchName}`;
  // 图片处理
  const responseImg = await axios.get(githubApiUrl, {
    headers: {
      Authorization: "token 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", // 不要这样做！
    },
  });
  console.log("tongguoyangzheng ");
  imagesList.value = responseImg.data.map((image) => {
    return {
      name: image.name, // 图片文件名，例如 "1.jpg"
      url: image.download_url, // 图片的下载URL
    };
  });
  // 读取文件
  const txtFolderPath = "textes"; // 仓库中存放图片的文件夹路径
  const txtApiUrl = `https://api.github.com/repos/${repoPath}/contents/${txtFolderPath}?ref=${branchName}`;
  const responseTxt = await axios.get(txtApiUrl, {
    headers: {
      Authorization: "token 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", // 不要这样做！
    },
  });
  textList.value = responseTxt.data.map((text) => {
    return {
      name: text.name, // 文件名，例如 "1.txt"
      url: text.download_url, // 文件的下载URL
    };
  });
});
</script>

